            style="cursor: default;min-width: 35px"
          >2</button>
          <span class="step-text text-center">{{ multilanguage.fillcheckbook[language] }}</span>
        </div>
      </template>
      <template v-else>
        <div class="col-3">
          <button
            class="btn btn-success rounded-circle"
