          >1</button>
          <a style="cursor: pointer;" @click="goback('dispo')">
            <span class="step-text text-center">{{ multilanguage.selectcircuit[language] }}</span>
          </a>
        </div>
        <div class="col-3">
          <button class="btn btn-primary rounded-circle" style="cursor: default;min-width: 35px">2</button>
          <span class="step-text text-center">{{ multilanguage.configurecircuit[language] }}</span>
        </div>
        <div class="col-6">
          <button
            class="btn btn-dark rounded-circle"
            disabled="disabled"
            style="cursor: default;min-width: 35px"
          >3</button>
          <span class="step-text text-center">{{ multilanguage.fillcheckbook[language] }}</span>
        </div>
      </template>
    </div>

    <div class="row">
      <div class="col-12 mt-5">
        <div class="circuittitle">{{ title }}</div>
      </div>
    </div>
    <form>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-6">
          <Ficha :rate="this.$route.query.key"></Ficha>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-6">
          <div class="calendar-title">{{multilanguage.changedates[language]}}</div>
          <div v-if="loaderdate" class="text-center mt-5 pt-5">
            <div class="spinner-border text-primary"></div>
          </div>
          <div v-else>
            <div v-if="charged" class="my-calendar-wrapper">
              <v-date-picker
                width="100%"
                mode="single"
                is-double-paned
                v-model="selectedValue"
                :is-linked="true"
                :is-expanded="true"
                :available-dates="availabledates"
                :disabled-dates="disableddates"
                :is-inline="true"
                :disabled-attribute="disableattr"
                :drag-attribute="dragattr"
                :select-attribute="selectattr"
                :theme-styles="themeStyles"
                tint-color="#FFE259"
                show-caps
              ></v-date-picker>
<div  class="">
              <div class="row calendar-legend">
                <div class="col">
                  <div
                    id="availablecolor"
                    class="box available"
                    :style="'background:'+ availablecolor"
                  ></div>
                  <div class="text-legend">{{ multilanguage.available[language] }}</div>
                </div>
                <div class="col">
                  <div class="box noavailable" :style="'background:'+ noavailablecolor"></div>
                  <div class="text-legend">{{ multilanguage.noavailable[language] }}</div>
                </div>
                <div class=" col-md col-xl">
                  <div class="box selected" :style="'background:'+ selectedcolor"></div>
                  <div class="text-legend">{{ multilanguage.selected[language] }}</div>
                </div>
              </div>
</div>
              <div class="form-group text-center">
                <input
                  data-error="wrong"
                  id="checkindates"
                  class="validate form-control"
                  style="display:none"
                />
                <div class="invalid-feedback">
                  <span id="checkindatesaux-msg"></span>
                </div>
              </div>
            </div>

            <div v-else class="col-12">
              <template v-if="showwhite">
                <a
                  @click="goback('dispo')"
                  style="cursor: pointer"
                >{{ multilanguage.nocalendar[language] }}</a>
              </template>
              <template v-else>
                <a
                  @click="goback('dispo')"
                  style="cursor: pointer"
                >{{ multilanguage.nocalendar[language] }}</a>
              </template>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <template v-if="detalles">
          <div class="col-sm-12 col-md-12 col-lg-6 mt-5">
            <div class="calendar-title text-center">
              {{
              multilanguage.configurecircuit[language]
              }}
            </div>
            <div class="row">
              <div class="col-12 form-group">
               <div style="position:relative;">
                <div @click="hidevariant=false">
                  <label for="variante" class="active">{{multilanguage.variant[language]}}</label>
                  <input
                    class="form-control"
                    readonly
                    id="variante"
                    v-model="variante.name"
                    @click ="listenEsc()"
                    style="background-color:white;"
                  />
                </div>
                <div class="list-group select-container" :class="{invisible: hidevariant}" >
                  <div
                    @click="variante = d"
                    v-bind:class="{active :variante === d}"
                    v-for="(d,indexv) in detalles.variants"
                    v-bind:value="d"
                    class="btn list-group-item list-group-item-action"
                    style="cursor: pointer"
                    :key="'k'+indexv"
                  >
                    <div class="row">
                      <div class="col text-left align-middle resalted">{{ d.name }}</div>
                      <template v-if="d.bestDeal.offer !== true">
                        <div
                          class="col text-right align-middle resalted"
                        >{{ d.bestDeal.retailPrice.value }} {{ d.bestDeal.retailPrice.value.currencyIsoCode }}</div>
                      </template>
                      <template v-else>
                        <div class="col text-right resalted">
                          <i
                            class="tiny material-icons mini-icon align-middle"
                            data-toggle="tooltip"
                            data-placement="bottom"
                            :title="d.bestDeal.offerText"
                            style="cursor: pointer;padding-right: 5px;"
                          >new_releases</i>
                          <span
                            class="align-middle"
                          >{{ d.bestDeal.retailPrice.value }} {{ d.bestDeal.retailPrice.value.currencyIsoCode }}</span>
                          <!--<i v-if="ocupacion.seleccionado === price.key" class="small material-icons" >check_box</i>-->
                        </div>
                      </template>
                    </div>
                    <div class="row">
                      <div class="col">{{ d.description }}</div>
                    </div>
                  </div>
                </div>
                </div>
               <!-- 
                  <label for="variante">
                  {{
                  multilanguage.variant[language]
                  }}
                </label>
                 <select id="variante" class="form-control" v-model="variante">
                  <option v-for="(d,indexd) in detalles.variants" v-bind:value="d" :key="indexd">
                    <div>
                      <b>{{ d.name }}</b>
                      <template v-if="d.offer !== true">
                        <span class="secondary-content black-text">
                          <b>{{ d.bestDeal.retailPrice.value }}</b>
                          {{ d.bestDeal.retailPrice.currencyIsoCode }}/
                          {{ d.pricePer }}
                        </span>
                      </template>
                      <template v-else>
                        <span class="badge red white-text">
                          <b>{{ d.bestDeal.retailPrice.value }}</b>
                          {{ d.bestDeal.retailPrice.currencyIsoCode }}/
                          {{ d.pricePer }}
                        </span>
                      </template>
                    </div>
                    <div>{{ d.description }}</div>
                  </option>
                </select>-->
                <div class="invalid-feedback">
                  <span id="varianteaux-msg"></span>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="col-6 form-group">
                <label for="adultos">{{ multilanguage.adult[language] }}</label>
                <input
                  class="form-control"
                  type="number"
                  id="adultos"
                  v-model="key.adultos"
                  min="0"
                  max="99"
                />
                <div class="invalid-feedback">
                  <span id="adultosaux-msg"></span>
                </div>
              </div>

              <div class="col-6 form-group">
                <label for="ninos">
                  {{
                  multilanguage.children[language]
                  }}
                </label>
                <input
                  class="form-control"
                  type="number"
                  id="ninos"
                  v-model="key.ninos"
                  min="0"
                  max="99"
                />
              </div>
            </div>
          </div>
          <div class="col-sm-12 col-md-12 col-lg-6 mt-5">
            <div class="calendar-title text-center">
              {{
              multilanguage.prices[language]
              }}
            </div>
            <div class="row" v-if="total">
              <div class="col-12">
                <table class="table mt-4 mb-4">
                  <thead>
                    <tr>
                      <th>{{ multilanguage.description[language] }}</th>
                      <th class="text-right">{{ multilanguage.value[language] }}</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-if="total.value" style="margin-top:30px;">
                      <td class="pt-3">{{ multilanguage.total[language] }}</td>
                      <td class="text-right pt-3">
                        {{ total.value.value }}
                        {{ total.value.currencyIsoCode }}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <hr class="pt-3" />
              </div>
              <div class="col-12 text-center button-container" v-if="total.available">
                <!-- <i class="material-icons green small circle">done</i><br/>{{multilanguage.available[language]}}-->
                <button
                  type="button"
                  class="btn btn-primary"
                  v-on:click="book"
                >{{ multilanguage.goon[language] }}</button>
              </div>
              <div class="col-12 text-center" v-else>
                <button
                  type="button"
                  class="btn btn-primary"
                  disabled
                >{{ multilanguage.noavailable[language] }}</button>
              </div>
            </div>
          </div>
        </template>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import helper from "@/script/helper";
import Ficha from "@/components/commons/Ficha";

var globallanguage = "es";
if (globaldata.language != null && globaldata.language != "") {
  globallanguage = globaldata.language;
}

var iswhitebrand = false;

if (
  globaldata.whitebrandcircuit !== null &&
  globaldata.whitebrandcircuit !== ""
) {
  iswhitebrand = true;
}

const langfile = require("../../assets/lang/content.json");

var today = new Date();
var tomorrow = new Date();
tomorrow.setDate(tomorrow.getDate() + 1);

export default {
  name: "Rates",
  data: function() {
    return {
      hidevariant: true,
      availablecolor: globaldata.availablecolor
        ? globaldata.availablecolor
        : "#4caf50",
      noavailablecolor: globaldata.noavailablecolor
        ? globaldata.noavailablecolor
        : "#F84744",
      selectedcolor: globaldata.selectedcolor
        ? globaldata.selectedcolor
        : "#ffc107",
      multilanguage: langfile.multilanguage,
      today: new Date(),
      charged: null,
      showwhite: iswhitebrand,
      title: this.$route.query.circuitname,

      detalles: null,
      availabledates: [],
      selectedValue: this.getInitialDate("entrada"),
      selectedfecha: -1,
      disableddates: null,
      variante: null,
      variantes: [],
      key: {
        key: null,
        ratekey: null,
        datekey: null,
        variantesel: null,
        varianteselname: null,
        adultos: 1,
        ninos: 0
      },
      total: null,
      loaderdate: null,
      language: globaldata.language
    };
  },
  components: {
    Ficha: Ficha
  },
  computed: {
    themeStyles() {
      return {
        wrapper: {
          background: "#fff",
          borderBotom: "solid rgba(0, 0, 0, 0.2) 1px"
          /*border: "solid rgba(0, 0, 0, 0.2) 1px",
          borderRadius: "5px",
          padding: "10px"*/
        },
        headerTitle: {
          padding: "10px 0px",
          fontSize: "25px"
        },
        dayCellNotInMonth: {
          opacity: 0.7
        },
        dayCell: {
          background: this.availablecolor, //"#4caf50",
          color: globaldata.availabletext ? globaldata.availabletext : "white", //"#fff",
          padding: "0px 0px",
          margin: "0px"
        },
        dayContent: {
          padding: "0px",
          margin: "0px",
          fontSize: "16px"
        }
      };
    },
    disableattr() {
      return {
        key: "disabled",
        highlight: {
          backgroundColor: this.noavailablecolor, //"#F84744",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: globaldata.noavailabletext
            ? globaldata.noavailabletext
            : "white"
        }
      };
    },
    enableattr() {
      return {
        key: "available",
        highlight: {
          backgroundColor: this.availablecolor, // "#4caf50",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: globaldata.noavailabletext
            ? globaldata.noavailabletext
            : "white"
        }
      };
    },
    dragattr() {
      return {
        key: "drag",
        highlight: {
          backgroundColor: this.selectedcolor, // "#ffc107",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: globaldata.selectedtext ? globaldata.selectedtext : "black"
        }
      };
    },
    selectattr() {
      return {
        key: "select",
        highlight: {
          backgroundColor: this.selectedcolor, //"#ffb300",
          opacity: 1,
          animated: false
        },
        contentStyle: {
          color: globaldata.selectedtext ? globaldata.selectedtext : "black"
        }
      };
    }
  },
  watch: {
    variante: function(val) {
      if (this.$data.variante) {
        this.$data.key.variantesel = this.$data.variante.key;
        this.$data.key.varianteselname = this.$data.variante.name;
        this.valorar();
      }
    },
    "key.ninos": function(val) {
      this.valorar();
    },
    "key.adultos": function(val) {
      this.valorar();
    },

    selectedValue: function(val) {
      this.$data.key.datekey = this.formatDateInt(val);
      this.getRates();
    }
  },
  methods: {
     listenEsc: function(e){
      document.addEventListener("keydown", this.closeselect, false);
      document.addEventListener("click",this.closeselect, true);
    },
    closeselect: function(e) {
      var keyCode = e.keyCode;
        if(keyCode === 27 || (e.type ==="click" && this.$data.hidevariant ===false)){
         this.$data.hidevariant = true
         this.removeListener()
        }
    },
    removeListener: function(){
      document.removeEventListener("keydown", this.closeselect)
    },
    
    valorar: function() {
      var d = this.$data;
      if (
        d.key.datekey &&
        d.key.datekey !== "" &&
        d.key.variantesel &&
        d.key.variantesel !== ""
      ) {
        axios({
          method: "get",
          url: globaldata.baseurl + "/circuit/check/" + this.$route.query.key,
          params: {
            language: d.language,
            adults: d.key.adultos,
            children: d.key.ninos,
            date: d.key.datekey,
            variant: d.key.variantesel
          }
        })
          .then(function(response) {
            console.log(response.data);
            d.total = response.data;
            d.key.ratekey = d.total.key;
          })
          .catch(function(error) {
            d.total = null;
            d.key.ratekey = null;
            console.log(error);
          });
      } else {
        d.total = null;
        d.key.ratekey = null;
      }
    },
    goback(page) {
      switch (page) {
        case "dispo":
          this.$router.push({
            name: "main",
            query: {
              idioma: this.$data.language
            }
          });
          break;
        case "rates":
          this.$router.push({
            name: "circuit_rates",
            query: {
              key: this.$route.query.key,
              entrada: this.$route.query.entrada,
              iddestino: this.$route.query.iddestino,
              destino: this.$route.query.destino,
              circuitType: this.$route.query.circuitType,
              idioma: this.$data.language,
              circuitname: this.$route.query.circuitname
            }
          });
          break;
        default:
      }
    },
    book: function(e) {
      e.preventDefault();
      if (this.checkForm()) {
        this.$router.push({
          name: "circuito_detalle",
          query: {
            key: this.$route.query.key,
            ratekey: this.$data.key.ratekey,
            datekey: this.$data.key.datekey,
            variantesel: this.$data.key.variantesel,
            varianteselname: this.$data.key.varianteselname,
            adultos: this.$data.key.adultos,
            ninos: this.$data.key.ninos,
            circuitname: this.$route.query.circuitname,
            entrada:
              this.$data.key.datekey.toString().substr(0, 4) +
              "-" +
              this.$data.key.datekey.toString().substr(4, 2) +
              "-" +
              this.$data.key.datekey.toString().substr(6, 2)
          }
        });
      }
    },

    checkForm: function(e) {
      var existsErrors = false;
      this.cleanErrors();

      if (this.key.datekey === null || this.key.datekey === "") {
        this.setValidationError(
          "checkindates",
          "checkindatesaux-msg",
          this.multilanguage.errordateempty[this.language]
        );
        existsErrors = true;
      }

      if (this.key.variantesel === null || this.key.variantesel === "") {
        this.setValidationError(
          "variante",
          "varianteaux-msg",
          this.multilanguage.errorvariantempty[this.language]
        );
        existsErrors = true;
      }

      if (this.key.adultos < 1) {
        this.setValidationError(
          "adultos",
          "adultosaux-msg",
          this.multilanguage.errornopeople[this.language]
        );
        existsErrors = true;
      }

      if (existsErrors) {
        return false;
      }
      return true;
    },
    setValidationError(field, msgField, msg) {
      var d = this.$data;

      var element = document.getElementById(field);
      element.classList.add("is-invalid");
      element.classList.add("form-control");
      element.focus();

      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    cleanErrors: function() {
      var d = this.$data;

      var element = document.getElementById("variante");
      element.setAttribute("class", "form-control bg-white");

      var element = document.getElementById("adultos");
      element.setAttribute("class", "form-control bg-white");

      element = document.getElementById("checkindates");
      element.setAttribute("class", "form-control bg-white");
    },
    getavailabledates: function(e) {
      var d = this.$data;
      var _this = this;
      d.loaderdate = true;

      axios({
        method: "get",
        url: globaldata.baseurl + "/cms/circuitavailabilitycalendar",
        params: {
          circuit: this.$route.query.key
          // checkin: this.$route.query.entrada ? this.$route.query.entrada.replace(/-/g, '') : null,
        }
      })
        .then(function(response) {
          var isFirst = true;
          var startdate;
          var enddate;
          var lastdate;
          var lastdateTrans;

          d.loaderdate = null;

          d.disponibilidad = response.data;

          if (d.disponibilidad) {
            for (var i = 0; i < d.disponibilidad.months.length; i++) {
              for (
                var j = 0;
                j < d.disponibilidad.months[i].weeks.length;
                j++
              ) {
                for (
                  var k = 0;
                  k < d.disponibilidad.months[i].weeks[j].days.length;
                  k++
                ) {
                  if (
                    d.disponibilidad.months[i].weeks[j].days[k].date !== null &&
                    d.disponibilidad.months[i].weeks[j].days[k].styleName ===
                      "av"
                  ) {
                    if (isFirst) {
                      startdate =
                        d.disponibilidad.months[i].weeks[j].days[k].date;
                      enddate = null;
                      isFirst = false;
                    } else {
                      var actualDate = new Date(
                        d.disponibilidad.months[i].weeks[j].days[k].date
                      );
                      actualDate.setDate(actualDate.getDate() - 1);
                      actualDate.setMilliseconds(0);
                      actualDate.setSeconds(0);
                      actualDate.setMinutes(0);
                      actualDate.setHours(0);
                      if (actualDate.getTime() !== lastdateTrans.getTime()) {
                        var ldate = lastdateTrans;
                        ldate.setDate(ldate.getDate());
                        enddate = _this.formatDate(ldate);
                        // enddate = lastdate

                        d.availabledates.push({
                          start: startdate,
                          end: enddate
                        });
                        startdate =
                          d.disponibilidad.months[i].weeks[j].days[k].date;
                      }
                    }
                    lastdate = d.disponibilidad.months[i].weeks[j].days[k].date;
                    lastdateTrans = new Date(
                      d.disponibilidad.months[i].weeks[j].days[k].date
                    );

                    lastdateTrans.setMilliseconds(0);
                    lastdateTrans.setSeconds(0);
                    lastdateTrans.setMinutes(0);
                    lastdateTrans.setHours(0);
                  }
                }
              }
            }

            var _ldate = new Date(lastdate);
            _ldate.setDate(_ldate.getDate() + 1);
            enddate = _this.formatDate(_ldate);

            if (startdate && enddate) {
              d.availabledates.push({ start: startdate, end: enddate });
            }
          }

          if (d.availabledates.length === 0) {
            d.availabledates = null;
            d.disableddates = { start: null, end: null };
          }

          d.charged = true;
        })
        .catch(function(error) {
          d.charged = false;
          d.loaderdate = null;
          console.log(error);
        });
    },

    formatDate: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
    formatDateInt: function(date) {
      var d = new Date(date);
      var month = "" + (d.getMonth() + 1);
      var day = "" + d.getDate();
      var year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("");
    },
    getRates: function() {
      var d = this.$data;
      var _this = this;
      axios({
        method: "get",
        url: globaldata.baseurl + "/circuit/rates/" + this.$route.query.key,
        params: {
          date: _this.formatDateInt(d.selectedValue),
          language: d.language
        }
      })
        .then(function(response) {
          d.detalles = response.data;
          d.charged = true;
          d.variantes = d.detalles.variants;
           
          if (d.variantes) {
            d.variante = d.variantes[0];
              
          } else {
            d.variante = "";
            d.key.variantesel = null;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getInitialDate: function(dt) {
      let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
      if (dt === "entrada") {
        if (this.$route.query.entrada) {
          return new Date(this.$route.query.entrada);
        } else {
          if (maindata && maindata.startdate) {
            return new Date(maindata.startdate);
          } else {
            return today;
          }
        }
      }
      if (dt === "salida") {
        if (this.$route.query.salida) {
          return new Date(this.$route.query.salida);
        } else {
          if (maindata && maindata.enddate) {
            return new Date(maindata.enddate);
          } else {
            return tomorrow;
          }
        }
      }
    }
  },
  mounted() {
    var d = this.$data;
    d.key.ratekey = this.$route.query.key;
    d.key.datekey = this.formatDateInt(d.selectedValue);
    this.getRates();
    this.getavailabledates();
    helper.scrollToId("main-app");
  }
};
</script>

