import Vue from "vue";
import VueAxios from "vue-axios";
import axios from "axios";
import Router from "vue-router";
import Reserva from "@/components/commons/Reserva";
import Ok from "@/components/commons/Ok";
import Ko from "@/components/commons/Ko";
import CircuitoDispo from "@/components/circuito/Dispo";
import CircuitoRates from "@/components/circuito/Rates";
import CircuitoDetalle from "@/components/circuito/Detalle";

import VCalendar from "v-calendar";
import "v-calendar/lib/v-calendar.min.css";

Vue.use(Router);
Vue.use(VueAxios, axios);
Vue.use(VCalendar, {
  firstDayOfWeek: 2, // Monday
  locale: globaldata.language
});

export default new Router({
  routes: [
    {
      path: "/",
      name: "reserva",
      component: Reserva,
      children: [
        {
          path: "",
          name: "main",
          component: CircuitoDispo
        },

        {
          path: "rates",
          name: "circuito_rates",
          component: CircuitoRates
        },
        {
          path: "detalle",
          name: "circuito_detalle",
          component: CircuitoDetalle
        },
        {
          path: "ok",
          name: "ok",
          component: Ok
        },
        {
          path: "ko",
          name: "ko",
          component: Ko
        }
      ]
    }
  ]
});
