<script>
import axios from "axios";
import helper from '@/script/helper'
import noUiSlider from "nouislider";
import "nouislider/distribute/nouislider.css";
import wNumb from "wnumb";

var slider;

const langfile = require("../../assets/lang/content.json");

export default {
  name: "CircuitoDispo",
  data: function() {
    return {
      dispo: null,
      loader: null,
      maxPrice: null,
      minPrice: null,
      language: globaldata.language?globaldata.language:"es",
      multilanguage: langfile.multilanguage,
      pricesrange: null,
      hidefilter: true,
      labels: []
    };
  },
  watch: {
    labels: function(val) {
      this.fiterresults();
    }
  },
  methods: {
    abrirdetalle(k, name) {
      this.deletetooltips();

      this.$router.push({
        name: "circuito_rates",
        query: {
          key: k,
          idioma: this.$data.language,
          circuitname: name
        }
      });
    },

    fiterresults() {
      var d = this.$data;
      var _this = this;
      var prices = [null, null];
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        prices = slider.noUiSlider.get();
      }

      var selectedLables = "";
      for (var i = 0; i < d.labels.length; i++) {
        if (d.labels[i]) {
          selectedLables = selectedLables + d.dispo.labels[i].id + ",";
        }
      }

      axios({
        method: "get",
        url: globaldata.baseurl + "/circuit/filter",
        params: {
          resorts: this.$route.query.iddestino,
          checkin: this.$route.query.entrada
            ? this.$route.query.entrada.replace(/-/g, "")
            : null,
          language: this.$data.language,
          minprice: prices[0],
          maxprice: prices[1],
          labels: selectedLables === "" ? null : selectedLables
        }
      })
        .then(function(response) {
          d.loader = null;
          d.dispo = response.data;
        })
        .catch(function(error) {
          d.dispo = null;
          d.loader = null;
          console.log(error);
        });
    },
    resetfilter() {
      var _this = this;
      var d = this.$data;
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        slider.noUiSlider.reset();
      }

      _this.getAvailability();
    },
    deletetooltips() {
      var element = document.getElementsByClassName("material-tooltip"),
        index;
      for (index = element.length - 1; index >= 0; index--) {
        element[index].parentNode.removeChild(element[index]);
      }
    },
    
    getAvailability: function() {
      var d = this.$data;
      var _this = this;
      d.loader = true;
      axios({
        method: "get",
        url: globaldata.baseurl + "/circuit/available",
        params: {
          language: this.$data.language
        }
      })
        .then(function(response) {
          d.loader = false;
          console.log(response.data);
          d.dispo = response.data;

          for (var i = 0; i < d.dispo.labels.length; i++) {
            d.labels[i] = false;
          }

          d.minPrice = d.dispo.minPrice;
          d.maxPrice = d.dispo.maxPrice;

          setTimeout(function() {
            if (
              d.maxPrice >= 0 &&
              d.minPrice >= 0 &&
              d.maxPrice !== d.minPrice
            ) {
              var step = 10;

              if (
                d.maxPrice - d.minPrice < 50 &&
                d.maxPrice - d.minPrice >= 20
              ) {
                step = 5;
              } else if (d.maxPrice !== d.minPrice < 20) {
                step = 1;
              }

              slider = document.getElementById("test-slider");
              if (!slider.noUiSlider) {
                noUiSlider.create(slider, {
                  start: [
                    d.minPrice === 0 ? 0 : d.minPrice - 1,
                    d.maxPrice + 1
                  ],
                  connect: true,
                  step: step,
                  tooltips: [true, true],
                  pips: {
                    mode: "count",
                    values: 6,
                    density: 4
                  },
                  orientation: "horizontal", // 'horizontal' or 'vertical'
                  range: {
                    min: d.minPrice === 0 ? 0 : d.minPrice - 1,
                    max: d.maxPrice + 1
                  },
                  format: wNumb({
                    decimals: 0
                  })
                });
                slider.noUiSlider.on("change", function(values, handle) {
                  _this.fiterresults();
                });
              }
            }
          });
        })
        .catch(function(error) {
          d.loader = false;
          console.log(error);
        });
    }
  },
  mounted() {
    

    var d = this.$data;
    var _this = this;
    d.loader = true;

    _this.getAvailability();

   // helper.scrollToId("main-app");
  }
};
</script>

